import React, { useContext, useEffect, useState } from "react";
import { theme } from "../theme";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import socketClient from "socket.io-client";
import Box from "@material-ui/core/Box";
import ScaleIcon from "../assets/scaleIcon";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { FormControl, InputLabel, MenuItem, Select } from "@material-ui/core";
// import useSWR from "swr";

import axios from "axios";
import Copyright from "../components/copyright";
import { AuthContext } from "../contexts/auth";
// import fetcher from "../services/fetcher";

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function Login() {
  const classes = useStyles();
  const [ipServer, setIpServer] = useState("localhost");
  const [weight, setWeight] = useState(0);
  const [connectionStatus, setConnectionStatus] = useState(false);
  const [connectionStatusServer, setConnectionStatusServer] = useState(false);
  const [serialPortName, setSerialPortName] = useState("");
  const [intervalState, setIntervalState] = useState();
  const [serialPortsList, setSerialPortsList] = useState(["COM1", "COM3"]);

  const { setIsSigned } = useContext(AuthContext);

  useEffect(() => {
    setWeight(0);
    setConnectionStatus(false);
    // console.log("interval cleared");
    // if (serialPortName === "COM3") {
    initConnectionWithInnerAPI();
    // }
  }, [serialPortName]);

  useEffect(() => {
    axios.get("http://localhost:3001/serialportlist").then((res) => {
      console.log(`res.data`, res.data.list);
      res.data.list.push("COM3");
      setSerialPortsList(res.data.list);
    });

    var socket = socketClient("http://localhost:3001");
    socket.on("weight", (data) => {
      console.log(`data`, data);
      setWeight(data.weight);
      setConnectionStatus(true);
    });
  }, []);

  function initConnectionWithInnerAPI(e) {
    e?.preventDefault();
    let urlServer = "http://" + ipServer + ":3001";
    console.log(`\n\nip server ===> `, urlServer);

    // setIntervalState(
    // setInterval(() => {
    console.log(`urlServer`, urlServer);
    axios
      .post(urlServer + "/setport", {
        choosenPort: serialPortName,
      })
      .then((res) => {
        console.log(`res.data.weight`, res.data.weight);
        setWeight(res.data.weight);
        setConnectionStatus(true);
      })
      .catch((error) => {
        console.log(`error`, error);
        setWeight(0);
        setConnectionStatus(false);
      });
    // }, 200)
    // );

    // return () => clearInterval(intervalState);
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <ScaleIcon
          color={connectionStatus ? "green" : "red"}
          width={100}
          height={100}
        />
        <Typography component="h1" variant="h5">
          Peso: {weight !== undefined ? weight : "--,--"} KG
        </Typography>
        <form className={classes.form} noValidate>
          <FormControl
            fullWidth
            variant="outlined"
            className={classes.formControl}
          >
            <InputLabel id="demo-simple-select-outlined-label">
              Puerta Serial
            </InputLabel>
            <Select
              labelId="demo-simple-select-outlined-label"
              id="demo-simple-select-outlined"
              value={serialPortName}
              onChange={(e) => setSerialPortName(e.target.value)}
              label="Puerta Serial"
            >
              <MenuItem value="Ninguna">
                <em>Ninguna</em>
              </MenuItem>
              {serialPortsList.map((portName) => {
                return <MenuItem value={portName}>{portName}</MenuItem>;
              })}
            </Select>
          </FormControl>

          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            label="IP del Server"
            name="email"
            autoFocus
            value={ipServer}
            onChange={(e) => {
              setIpServer(e.target.value);
            }}
          />

          <Button
            // type="submit"
            fullWidth
            variant="contained"
            style={{
              backgroundColor: connectionStatusServer ? "green" : theme.primary,
              color: "white",
            }}
            className={classes.submit}
            onClick={() => {
              setConnectionStatusServer(!connectionStatusServer);
            }}
          >
            {connectionStatusServer
              ? "Conectado al Server"
              : "Conectar con el Server"}
          </Button>
          <Button
            // type="submit"
            fullWidth
            variant="contained"
            style={{ backgroundColor: "red", color: "white", marginTop: -5 }}
            className={classes.submit}
            onClick={() => {
              setIsSigned(false);
            }}
          >
            Logout
          </Button>
        </form>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
}
